"use client";

import React, { useState, useEffect, useRef } from "react";
import {
  Bot,
  Sparkles,
  Loader2,
  Copy,
  Check,
  Send,
  PenTool,
  RotateCcw,
  History,
  ChevronLeft,
  ChevronRight,
  Maximize2,
  Minimize2,
  Wand2,
  Briefcase,
  Coffee,
  Zap,
  Heart,
  Feather,
  Layout,
  Type,
  AlignLeft,
  MoreHorizontal,
  X,
  Trash2,
  Save,
  SplitSquareHorizontal // Added to ensure no import errors
} from "lucide-react";

// --- Types ---

interface EmailState {
  recipient: string;
  subject: string;
  keyPoints: string;
  tone: string;
  style: string;
  length: string;
  options: {
    includeCTA: boolean;
    addUrgency: boolean;
    humanize: boolean;
    formal: boolean;
    simplify: boolean;
    addWarmth: boolean;
  };
}

interface DraftVersion {
  id: string;
  content: string;
  timestamp: Date;
  label: string;
}

// --- Configuration Constants ---

const TONES = [
  { id: "Professional", icon: Briefcase, color: "text-blue-400", bg: "bg-blue-400/10" },
  { id: "Casual", icon: Coffee, color: "text-amber-400", bg: "bg-amber-400/10" },
  { id: "Urgent", icon: Zap, color: "text-red-400", bg: "bg-red-400/10" },
  { id: "Empathetic", icon: Heart, color: "text-pink-400", bg: "bg-pink-400/10" },
];

const STYLES = ["Direct", "Storytelling", "Cold Outreach", "Assertive", "Diplomatic"];
const LENGTHS = ["Short", "Medium", "Long"];

// --- Utilities ---

// CRITICAL: Safe output formatter to prevent object rendering crashes
function formatOutput(item: any): string {
  if (item === null || item === undefined) return "";
  if (typeof item === "string") return item;
  
  if (Array.isArray(item)) {
    return item.map(formatOutput).join("\n\n");
  }

  if (typeof item === "object") {
    // Handle specific keys if the API returns structured JSON
    if (item.result) return formatOutput(item.result);
    if (item.content) return formatOutput(item.content);
    if (item.email) return formatOutput(item.email);
    if (item.body) return formatOutput(item.body);
    
    // Fallback: stringify
    return JSON.stringify(item, null, 2).replace(/["{}]/g, "");
  }
  return String(item);
}

// --- Main Component ---

export default function EmailAssistantPage() {
  // --- State ---
  const [formData, setFormData] = useState<EmailState>({
    recipient: "",
    subject: "",
    keyPoints: "",
    tone: "Professional",
    style: "Direct",
    length: "Medium",
    options: {
      includeCTA: false,
      addUrgency: false,
      humanize: true,
      formal: false,
      simplify: false,
      addWarmth: false,
    },
  });

  const [isLoading, setIsLoading] = useState(false);
  const [isRewriting, setIsRewriting] = useState(false);
  const [versions, setVersions] = useState<DraftVersion[]>([]);
  const [activeVersionIdx, setActiveVersionIdx] = useState<number>(-1);
  const [showHistory, setShowHistory] = useState(false);
  const [copied, setCopied] = useState(false);
  const [historyList, setHistoryList] = useState<DraftVersion[]>([]);

  // --- Handlers ---

  const handleInputChange = (field: keyof EmailState, value: any) => {
    setFormData((prev) => ({ ...prev, [field]: value }));
  };

  const handleOptionToggle = (key: keyof typeof formData.options) => {
    setFormData((prev) => ({
      ...prev,
      options: { ...prev.options, [key]: !prev.options[key] },
    }));
  };

  const handleGenerate = async () => {
    setIsLoading(true);
    
    // Smart Prompt Injection: Pack settings into context for the backend
    const enhancedContext = `
      [Strict Configuration]
      - Style: ${formData.style}
      - Length: ${formData.length}
      - Modifiers: ${Object.entries(formData.options).filter(([_, v]) => v).map(([k]) => k).join(", ")}
      
      [User Context]
      ${formData.keyPoints}
    `;

    try {
      const res = await fetch("/api/bots/email-assistant", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          recipient: formData.recipient,
          subject: formData.subject,
          keyPoints: enhancedContext, // Sending config inside keyPoints
          tone: formData.tone
        }),
      });
      
      const data = await res.json();
      
      if (data.result) {
        const cleanText = formatOutput(data.result);
        const newVersion: DraftVersion = {
          id: crypto.randomUUID(),
          content: cleanText,
          timestamp: new Date(),
          label: `Draft ${versions.length + 1}`
        };
        
        const newVersions = [...versions, newVersion];
        setVersions(newVersions);
        setActiveVersionIdx(newVersions.length - 1);
        
        // Add to history
        setHistoryList(prev => [newVersion, ...prev]);
      }
    } catch (error) {
      console.error("Generation failed", error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleRewrite = async (instruction: string) => {
    if (activeVersionIdx === -1) return;
    setIsRewriting(true);

    const currentContent = versions[activeVersionIdx].content;
    const rewriteContext = `
      ORIGINAL EMAIL:
      ${currentContent}

      INSTRUCTION:
      ${instruction}
      
      Keep the same context, just apply the instruction.
    `;

    try {
      // Re-using the main endpoint for rewriting by passing specific context
      const res = await fetch("/api/bots/email-assistant", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          recipient: formData.recipient,
          subject: "REWRITE_REQUEST", 
          keyPoints: rewriteContext,
          tone: formData.tone
        }),
      });

      const data = await res.json();

      if (data.result) {
        const cleanText = formatOutput(data.result);
        const newVersion: DraftVersion = {
          id: crypto.randomUUID(),
          content: cleanText,
          timestamp: new Date(),
          label: `Rewrite: ${instruction.split(' ')[0]}...`
        };
        
        const newVersions = [...versions, newVersion];
        setVersions(newVersions);
        setActiveVersionIdx(newVersions.length - 1);
      }
    } catch (e) {
      console.error(e);
    } finally {
      setIsRewriting(false);
    }
  };

  const copyText = () => {
    if (activeVersionIdx === -1) return;
    navigator.clipboard.writeText(versions[activeVersionIdx].content);
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  };

  const clearAll = () => {
    setVersions([]);
    setActiveVersionIdx(-1);
    setFormData({ ...formData, keyPoints: "", subject: "", recipient: "" });
  };

  // --- Render ---

  return (
    <div className="min-h-screen bg-[#0B1120] text-slate-200 font-sans p-4 md:p-6 lg:p-8">
      
      {/* Top Header */}
      <div className="max-w-[1600px] mx-auto mb-8 flex justify-between items-center">
        <div className="flex items-center gap-4">
          <div className="p-3 rounded-2xl bg-[#1E293B] border border-slate-700/50 shadow-lg shadow-green-900/10">
            <Bot className="w-8 h-8 text-[#00C26D]" />
          </div>
          <div>
            <h1 className="text-3xl font-bold text-white tracking-tight">Email Assistant</h1>
            <p className="text-slate-400">The world's most advanced AI email engine.</p>
          </div>
        </div>
        <div className="flex gap-3">
           <button 
             onClick={() => setShowHistory(!showHistory)}
             className={`p-3 rounded-xl border transition-all ${showHistory ? 'bg-[#00C26D]/10 border-[#00C26D] text-[#00C26D]' : 'bg-[#1E293B] border-slate-700 text-slate-400 hover:text-white'}`}
           >
             <History className="w-5 h-5" />
           </button>
        </div>
      </div>

      <div className="max-w-[1600px] mx-auto grid grid-cols-1 lg:grid-cols-12 gap-8">
        
        {/* === LEFT PANEL: SMART INPUT ENGINE === */}
        <div className="lg:col-span-4 space-y-6">
          <div className="bg-[#1E293B] border border-slate-700/50 rounded-3xl p-6 shadow-2xl relative overflow-hidden">
            {/* Background Glow */}
            <div className="absolute top-0 right-0 w-64 h-64 bg-[#00C26D]/5 rounded-full blur-3xl -z-10" />

            <div className="flex items-center gap-2 mb-6">
              <Sparkles className="w-5 h-5 text-[#00C26D]" />
              <h2 className="text-lg font-bold text-white uppercase tracking-wider">Context Engine</h2>
            </div>

            <div className="space-y-5">
              {/* Recipient & Subject */}
              <div className="grid grid-cols-1 gap-4">
                <Input 
                  label="Recipient" 
                  placeholder="e.g. Elon Musk" 
                  value={formData.recipient} 
                  onChange={(v: string) => handleInputChange("recipient", v)} 
                />
                <Input 
                  label="Subject Line (Optional)" 
                  placeholder="e.g. Partnership Opportunity" 
                  value={formData.subject} 
                  onChange={(v: string) => handleInputChange("subject", v)} 
                />
              </div>

              {/* Key Points */}
              <div>
                <label className="block text-xs font-bold text-slate-400 uppercase mb-2">
                  Core Message / Bullets
                </label>
                <textarea
                  value={formData.keyPoints}
                  onChange={(e) => handleInputChange("keyPoints", e.target.value)}
                  className="w-full bg-[#0F172A] border border-slate-700 rounded-xl p-4 text-sm text-white h-32 resize-none focus:ring-2 focus:ring-[#00C26D] focus:border-transparent outline-none transition-all placeholder-slate-600"
                  placeholder="- Ask for a meeting next Tuesday&#10;- Mention the Q3 report&#10;- Compliment their recent launch"
                />
              </div>

              {/* Tone Selector */}
              <div>
                <label className="block text-xs font-bold text-slate-400 uppercase mb-3">Tone</label>
                <div className="grid grid-cols-2 gap-3">
                  {TONES.map((t) => (
                    <button
                      key={t.id}
                      onClick={() => handleInputChange("tone", t.id)}
                      className={`flex items-center gap-3 p-3 rounded-xl border transition-all ${
                        formData.tone === t.id
                          ? `bg-[#0F172A] border-[#00C26D] ring-1 ring-[#00C26D]`
                          : "bg-[#0F172A] border-slate-700 hover:border-slate-600 opacity-70 hover:opacity-100"
                      }`}
                    >
                      <div className={`p-2 rounded-lg ${t.bg} ${t.color}`}>
                        <t.icon className="w-4 h-4" />
                      </div>
                      <span className={`text-sm font-medium ${formData.tone === t.id ? 'text-white' : 'text-slate-400'}`}>
                        {t.id}
                      </span>
                    </button>
                  ))}
                </div>
              </div>

              {/* Style & Length */}
              <div className="grid grid-cols-2 gap-4">
                <Select 
                  label="Writing Style" 
                  value={formData.style} 
                  options={STYLES} 
                  onChange={(v: string) => handleInputChange("style", v)} 
                />
                <Select 
                  label="Length" 
                  value={formData.length} 
                  options={LENGTHS} 
                  onChange={(v: string) => handleInputChange("length", v)} 
                />
              </div>

              {/* Advanced Toggles */}
              <div className="pt-4 border-t border-slate-700/50">
                <label className="block text-xs font-bold text-slate-500 uppercase mb-3">Refinements</label>
                <div className="grid grid-cols-2 gap-y-3 gap-x-4">
                  {Object.entries(formData.options).map(([key, val]) => (
                    <Toggle 
                      key={key} 
                      label={key.replace(/([A-Z])/g, ' $1').trim()} 
                      checked={val} 
                      onChange={() => handleOptionToggle(key as keyof typeof formData.options)} 
                    />
                  ))}
                </div>
              </div>

              {/* Generate Action */}
              <div className="pt-4">
                <button
                  onClick={handleGenerate}
                  disabled={isLoading || !formData.keyPoints}
                  className="w-full group relative overflow-hidden rounded-xl bg-[#00C26D] p-4 text-white font-bold shadow-xl shadow-green-900/20 transition-all hover:bg-[#00A05A] hover:scale-[1.02] active:scale-[0.98] disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  <div className="relative z-10 flex items-center justify-center gap-2">
                    {isLoading ? <Loader2 className="w-5 h-5 animate-spin" /> : <Sparkles className="w-5 h-5" />}
                    <span>{versions.length > 0 ? "Generate Alternative" : "Draft Email"}</span>
                  </div>
                  <div className="absolute inset-0 -translate-x-full group-hover:animate-[shimmer_1.5s_infinite] bg-gradient-to-r from-transparent via-white/20 to-transparent z-0" />
                </button>
              </div>

            </div>
          </div>
        </div>

        {/* === CENTER/RIGHT: PREMIUM OUTPUT ENGINE === */}
        <div className={`transition-all duration-500 ${showHistory ? 'lg:col-span-6' : 'lg:col-span-8'}`}>
          <div className="bg-[#1E293B] border border-slate-700/50 rounded-3xl shadow-2xl flex flex-col h-full min-h-[800px] overflow-hidden relative">
            
            {/* macOS Window Header */}
            <div className="bg-[#0F172A] px-6 py-4 flex items-center justify-between border-b border-slate-700/50">
              <div className="flex gap-2">
                <div className="w-3 h-3 rounded-full bg-red-500/80" />
                <div className="w-3 h-3 rounded-full bg-yellow-500/80" />
                <div className="w-3 h-3 rounded-full bg-green-500/80" />
              </div>
              <div className="flex items-center gap-2 text-xs font-medium text-slate-500 uppercase tracking-widest">
                <PenTool className="w-3 h-3" />
                Editor
              </div>
              <div className="w-16"></div> {/* Spacer for balance */}
            </div>

            {/* Version Tabs */}
            {versions.length > 0 && (
              <div className="bg-[#162032] border-b border-slate-700/50 px-2 flex items-center gap-1 overflow-x-auto no-scrollbar">
                {versions.map((v, idx) => (
                  <button
                    key={v.id}
                    onClick={() => setActiveVersionIdx(idx)}
                    className={`px-4 py-3 text-xs font-bold transition-all border-b-2 flex items-center gap-2 whitespace-nowrap ${
                      activeVersionIdx === idx
                        ? "border-[#00C26D] text-white bg-[#1E293B]"
                        : "border-transparent text-slate-500 hover:text-slate-300 hover:bg-[#1E293B]/50"
                    }`}
                  >
                    <span>V{idx + 1}</span>
                    {idx === 0 && <span className="px-1.5 py-0.5 bg-slate-700 rounded text-[9px] text-slate-300">ORIGINAL</span>}
                  </button>
                ))}
              </div>
            )}

            {/* Editor Area */}
            <div className="flex-1 p-8 bg-[#1E293B] overflow-y-auto relative">
              {isLoading || isRewriting ? (
                <div className="absolute inset-0 flex flex-col items-center justify-center bg-[#1E293B]/80 z-20 backdrop-blur-sm gap-4">
                  <div className="relative">
                    <div className="w-16 h-16 border-4 border-[#00C26D]/30 border-t-[#00C26D] rounded-full animate-spin"></div>
                    <div className="absolute inset-0 flex items-center justify-center">
                      <Sparkles className="w-6 h-6 text-[#00C26D] animate-pulse" />
                    </div>
                  </div>
                  <p className="text-slate-400 font-medium animate-pulse">{isRewriting ? "Refining your draft..." : "Crafting your email..."}</p>
                </div>
              ) : null}

              {activeVersionIdx !== -1 ? (
                <div className="animate-in fade-in slide-in-from-bottom-4 duration-500">
                  <div className="bg-[#0F172A] border border-slate-700 rounded-xl p-8 shadow-inner min-h-[500px] relative group">
                    <button
                      onClick={copyText}
                      className="absolute top-4 right-4 p-2 bg-slate-800 hover:bg-slate-700 rounded-lg text-slate-400 hover:text-white transition-all border border-slate-700 opacity-0 group-hover:opacity-100"
                    >
                      {copied ? <Check className="w-4 h-4 text-[#00C26D]" /> : <Copy className="w-4 h-4" />}
                    </button>
                    
                    <div 
                      className="prose prose-invert max-w-none text-slate-200 text-lg leading-relaxed whitespace-pre-wrap outline-none font-light"
                      contentEditable
                      suppressContentEditableWarning
                    >
                      {versions[activeVersionIdx].content}
                    </div>
                  </div>

                  {/* Smart Rewrite Toolbar */}
                  <div className="mt-6">
                    <div className="flex items-center gap-2 mb-3">
                      <Wand2 className="w-4 h-4 text-[#00C26D]" />
                      <span className="text-xs font-bold text-slate-500 uppercase">AI Rewrite Tools</span>
                    </div>
                    <div className="flex flex-wrap gap-2">
                      <RewriteButton icon={Minimize2} label="Shorten" onClick={() => handleRewrite("Make it shorter and more concise")} disabled={isRewriting} />
                      <RewriteButton icon={Maximize2} label="Expand" onClick={() => handleRewrite("Expand with more detail and context")} disabled={isRewriting} />
                      <RewriteButton icon={SmileIcon} label="Friendlier" onClick={() => handleRewrite("Make the tone warmer and friendlier")} disabled={isRewriting} />
                      <RewriteButton icon={Briefcase} label="Professional" onClick={() => handleRewrite("Make it more formal and professional")} disabled={isRewriting} />
                      <RewriteButton icon={Zap} label="Punchier" onClick={() => handleRewrite("Make it punchy and direct")} disabled={isRewriting} />
                      <RewriteButton icon={AlignLeft} label="Simplify" onClick={() => handleRewrite("Simplify the language to grade 5 level")} disabled={isRewriting} />
                    </div>
                  </div>
                </div>
              ) : (
                <div className="h-full flex flex-col items-center justify-center text-slate-600 gap-6 opacity-60">
                  <div className="w-24 h-24 rounded-full bg-slate-800/50 flex items-center justify-center border border-slate-700">
                    <Bot className="w-10 h-10" />
                  </div>
                  <div className="text-center max-w-md">
                    <h3 className="text-xl font-bold text-slate-400 mb-2">Ready to write</h3>
                    <p className="text-sm">Configure the settings on the left to generate your first world-class email draft.</p>
                  </div>
                </div>
              )}
            </div>

            {/* Bottom Actions */}
            {activeVersionIdx !== -1 && (
              <div className="p-4 bg-[#162032] border-t border-slate-700/50 flex justify-between items-center">
                <button onClick={clearAll} className="text-xs text-red-400 hover:text-red-300 flex items-center gap-1 px-3 py-2 rounded-lg hover:bg-red-400/10 transition-colors">
                  <Trash2 className="w-3 h-3" /> Clear Workspace
                </button>
                <div className="flex gap-3">
                  <button onClick={handleGenerate} className="text-xs text-slate-400 hover:text-white flex items-center gap-1 px-3 py-2 rounded-lg hover:bg-slate-700 transition-colors">
                    <RotateCcw className="w-3 h-3" /> Try Again
                  </button>
                  <button onClick={copyText} className="px-4 py-2 bg-[#00C26D] hover:bg-[#00A05A] text-white font-bold rounded-lg text-sm flex items-center gap-2 transition-all shadow-lg shadow-green-900/20">
                    {copied ? <Check className="w-4 h-4" /> : <Copy className="w-4 h-4" />}
                    {copied ? "Copied" : "Copy to Clipboard"}
                  </button>
                </div>
              </div>
            )}
          </div>
        </div>

        {/* === RIGHT PANEL: HISTORY (Conditional) === */}
        {showHistory && (
          <div className="lg:col-span-2 animate-in slide-in-from-right duration-300">
            <div className="bg-[#1E293B] border border-slate-700/50 rounded-3xl p-4 shadow-xl h-full overflow-hidden flex flex-col">
              <div className="flex items-center justify-between mb-4 pb-4 border-b border-slate-700/50">
                <h3 className="text-sm font-bold text-white uppercase tracking-wider flex items-center gap-2">
                  <History className="w-4 h-4 text-[#00C26D]" /> History
                </h3>
                <button onClick={() => setShowHistory(false)} className="text-slate-500 hover:text-white">
                  <X className="w-4 h-4" />
                </button>
              </div>
              
              <div className="flex-1 overflow-y-auto space-y-3 custom-scrollbar pr-1">
                {historyList.length > 0 ? historyList.map((item, i) => (
                  <div 
                    key={item.id} 
                    onClick={() => {
                      // Find if this version is already in main list, if not add it
                      const exists = versions.findIndex(v => v.id === item.id);
                      if (exists !== -1) {
                        setActiveVersionIdx(exists);
                      } else {
                        setVersions([...versions, item]);
                        setActiveVersionIdx(versions.length);
                      }
                    }}
                    className="p-3 bg-[#0F172A] hover:bg-slate-800 border border-slate-700 hover:border-[#00C26D] rounded-xl cursor-pointer transition-all group"
                  >
                    <div className="flex justify-between items-start mb-2">
                      <span className="text-[10px] font-bold text-[#00C26D] bg-[#00C26D]/10 px-1.5 py-0.5 rounded">
                        {item.label || `Draft ${historyList.length - i}`}
                      </span>
                      <span className="text-[10px] text-slate-500">
                        {item.timestamp.toLocaleTimeString([], {hour: '2-digit', minute:'2-digit'})}
                      </span>
                    </div>
                    <p className="text-xs text-slate-400 line-clamp-3 leading-relaxed group-hover:text-slate-300">
                      {item.content}
                    </p>
                  </div>
                )) : (
                  <div className="text-center text-slate-600 text-xs py-10">No history yet.</div>
                )}
              </div>
            </div>
          </div>
        )}

      </div>
    </div>
  );
}

// --- Sub-Components ---

const Input = ({ label, value, onChange, placeholder }: any) => (
  <div>
    <label className="block text-xs font-bold text-slate-400 uppercase mb-2 ml-1">{label}</label>
    <input
      type="text"
      value={value}
      onChange={(e) => onChange(e.target.value)}
      className="w-full bg-[#0F172A] border border-slate-700 rounded-xl px-4 py-3 text-sm text-white focus:ring-2 focus:ring-[#00C26D] focus:border-transparent outline-none transition-all placeholder-slate-600"
      placeholder={placeholder}
    />
  </div>
);

const Select = ({ label, value, options, onChange }: any) => (
  <div>
    <label className="block text-xs font-bold text-slate-400 uppercase mb-2 ml-1">{label}</label>
    <div className="relative">
      <select
        value={value}
        onChange={(e) => onChange(e.target.value)}
        className="w-full bg-[#0F172A] border border-slate-700 rounded-xl px-4 py-3 text-sm text-white focus:ring-2 focus:ring-[#00C26D] focus:border-transparent outline-none appearance-none cursor-pointer transition-all"
      >
        {options.map((o: any) => (
          <option key={o} value={o}>{o}</option>
        ))}
      </select>
      <div className="absolute right-4 top-3.5 pointer-events-none text-slate-500">
        <MoreHorizontal className="w-4 h-4 rotate-90" />
      </div>
    </div>
  </div>
);

const Toggle = ({ label, checked, onChange }: any) => (
  <div 
    onClick={onChange}
    className={`flex items-center justify-between p-2 rounded-lg cursor-pointer border transition-all ${checked ? 'bg-[#00C26D]/10 border-[#00C26D]/30' : 'bg-transparent border-transparent hover:bg-slate-800'}`}
  >
    <span className={`text-xs font-medium ${checked ? 'text-white' : 'text-slate-400'}`}>{label}</span>
    <div className={`w-8 h-4 rounded-full relative transition-colors ${checked ? 'bg-[#00C26D]' : 'bg-slate-700'}`}>
      <div className={`absolute top-0.5 w-3 h-3 bg-white rounded-full transition-all shadow-sm ${checked ? 'left-4.5' : 'left-0.5'}`} />
    </div>
  </div>
);

const RewriteButton = ({ icon: Icon, label, onClick, disabled }: any) => (
  <button
    onClick={onClick}
    disabled={disabled}
    className="flex items-center gap-1.5 px-3 py-1.5 bg-[#0F172A] border border-slate-700 hover:border-[#00C26D] rounded-lg text-xs text-slate-300 hover:text-white transition-all disabled:opacity-50"
  >
    <Icon className="w-3 h-3" />
    {label}
  </button>
);

// Simple Smile Icon component since Lucide might not export 'SmileIcon' directly under that name sometimes
